
import { Options, Vue } from 'vue-class-component';
import { debounce } from "../../../utils/functions";


@Options({
  props: {
    placeholder: {
      type: String,
      required: false,
      default: ''
    },
  },
  emits: ['search']
})
export default class SearchComponent extends Vue {

  public debounceChange = debounce<
      (value: string) => void
  >(
      (value: string) => this.$emit('search', value),
      700
  );
}
